import { createStore } from 'vuex'

export default createStore({
  state() {
    return {
      singleGroup: null,
      singleExercise: null
    }
  },
  mutations: {
    setGroup(state, payload) {
      state.singleGroup = payload
      localStorage.setItem('group', JSON.stringify(payload))
    },
    setExercise(state, payload) {
      state.singleExercise = state.singleGroup.exercises[payload]
      localStorage.setItem('exercise', JSON.stringify(payload))
    }
  },
  actions: {
  },
  modules: {
  }
})
