<template>
   <div class="bg-dark">
      <div class="img-descr">
         <img :src="singleGroup.img" alt="missing">
         <p>{{ singleGroup.descr }}</p>
      </div>

      <div v-for="(es, i) in singleGroup.exercises" :key="i" class="mb-5 mt-5 img-descr">
