            <h2>{{ es.name }} ({{ es.sinonimo }})</h2>
         </router-link>
         <img :src="es.img" alt="missing">
      </div>
   </div>

</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
   props: ['id', 'data'],
   computed: {
      ...mapState(['singleGroup'])
   },
   data() {
      return {
         tmp: this.data
      }
   },
   methods: {
    ...mapMutations(['setExercise']),
    update(index) {
      this.setExercise(index)
    }
  }
}
</script>

